body,
h1 {
  margin: 0;
  padding: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
}

body { font-family: sans-serif; padding: 1em; }
.ais-SearchBox { margin: 1em 0; }

#header {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: column;
            flex-direction: column;
}

/* #header > .ais-SearchBox {
   
} */

#content-body {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}

#content-body-left {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 1em;
    margin-right: 2em;
}

#content-body-right {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 4 1;
            flex: 4 1;
    -webkit-flex-direction: column;
            flex-direction: column;
}

#content-body-filler {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}

#search-box {
    background-color: blue;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.hit {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.quality-tag {
    text-align: right;
    background-color: rgb(255, 64, 64);
    position: absolute;
    padding: 0.3em;
    border-radius: 100%;
    color: white;
}

.ais-Hits {
    width: 100%;
}

.range-slider-label {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.range-slider-label > p {
    margin: 0em 0em;
}

#pagination {
    margin: 1em;
}
